<template>
  <div id="app">
    <form @submit.prevent="onFormSubmit()" ref="contactform">
      <div class="sign">
        <div class="signin">
          <h2>Sign in</h2>
          <el-input  placeholder="Email Adress" v-model="INusername"/>
          <el-input placeholder="Password" v-model="INpassword"/>
          <el-button type="primary" @click="signIn">login</el-button>
        </div>
        <div class="signup">
          <h2>Sign up</h2>
          <el-input  placeholder="Email Adress" v-model="UPusername"/>
          <el-input placeholder="Password" v-model="UPpassword"/>
          <el-select v-model="gender" placeholder="Gender">
            <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value"/>
          </el-select>
          Age:<el-input-number v-model="age" controls-position="right" :min="0" :max="100"/>
          <el-button type="primary" @click="signUp">register</el-button>
        </div>
      </div>
      <p>
        This site is protected by reCAPTCHA and the Google
        <a href="https://policies.google.com/privacy">Privacy Policy</a> and
        <a href="https://policies.google.com/terms">Terms of Service</a> apply.
      </p>
    </form>
    <!--<p>company:<br/><strong>{{company}}</strong></p>-->
    <router-view/>
  </div>
</template>

<script>
import firebase from 'firebase'
import axios from "axios"

export default {
  name: 'HelloWorld',
  data:()=>{
    return {
      UPusername: '',
      UPpassword: '',
      INusername: '',
      INpassword: '',
      message: '',
      userPageNum:'',
      logoutText:'',
      options: [{
        value: 'm',
        label: '男性(male)'
      }, {
        value: 'f',
        label: '女性(female)'
      }],
      gender: '',
      age: '',
       company:""
    }
  },
  methods: {
    signUp: function () {
      firebase.auth().createUserWithEmailAndPassword(this.UPusername, this.UPpassword)
      .then(user => {
        const database = firebase.database();
        const userData = "userData";
        database.ref(userData).push({
          email: this.UPusername,
          gender: this.gender,
          age: this.age
        });
        alert('Create account: ', user.email)
        this.INusername = this.UPusername
        this.INpassword = this.UPpassword
        this.signIn()
      })
      .catch(error => {
        alert(error.message)
      })
    },
    signIn: function () {
      firebase.auth().signInWithEmailAndPassword(this.INusername, this.INpassword).then(
        user => {
          this.$_setuseremail(this.INusername)
          this.setIdRequest(this.INusername)
          this.emailRequest(this.INusername)
          this.INusername = ''
          this.INpassword = ''
          this.UPusername = ''
          this.UPpassword = ''
          this.gender = ''
          this.age = ''
          this.$router.push('/about').catch(()=>{});
          console.log(user)
        },
        err => {
          alert(err.message)
        }
      )
    },
    async onFormSubmit() {
      // Wait until recaptcha has been loaded.
      await this.$recaptchaLoaded();
 
      // Execute reCAPTCHA with action "login".
      const token = await this.$recaptcha('login');
 
      // Prepare form data
      let formData = new FormData(this.$refs.contactform);
 
      // Appended token in formData
      formData.append('recaptcha-token', token);
 
      // Make an ajax request to your Laravel endpoint.
      axios.post('/your-form-endpoint', formData).then(
        (response) => {
          // handle response
          alert(response.message)
        },
        (error) => {
          // handle errors
          alert(error.message)
        }
      )
    },
    emailRequest: async function(username){
      const params = new URLSearchParams();
      params.append("name",username);
      const postEmail = await axios.post("http://localhost:8040/postEmail", params)
      this.userPageNum = postEmail.data
      if (postEmail.data == "空きがありませんでした"){
        this.$router.push('/stay').catch(()=>{});
      }else{
        this.$router.push('/about').catch(()=>{});
      }
    },
    setIdRequest: async function(username){
      const params = new URLSearchParams();
      params.append("name",username);
      const IDs = await axios.post("http://localhost:8040/setIDs", params)
      this.company = IDs.data
      console.log(IDs.data)
      this.$_setchannel(IDs.data.channels)
      this.$_setusername(IDs.data.username)
      this.$_setpassword(IDs.data.password)
    }
  }
}
</script>

<style>
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}

#nav {
  padding: 30px;
}

#nav a {
  font-weight: bold;
  color: #2c3e50;
}

#nav a.router-link-exact-active {
  color: #42b983;
}

h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
.signin, .signup{
  margin: 20px;
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
}
.sign {
  display: flex;
  justify-content: center;
  align-items: center;
}
input {
  margin: 5px 0;
  padding: 5px;
}
</style>
